body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif!important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@font-face {
  font-family: 'Orbitron';
  src: url(/react-portfolio/static/media/Orbitron-Regular.c801dd36.woff) format('woff'),
        url(/react-portfolio/static/media/Orbitron-Regular.371fd655.ttf) format('ttf'),
        url(/react-portfolio/static/media/Orbitron-Regular.2df4c8db.woff2) format('woff2'),
        url(/react-portfolio/static/media/Orbitron-Regular.20838451.eot) format('eot');
}

.header {
	padding: 10px 20px;
	background-color: #212529;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 3;
}
.logo__link {
	font-size: 18px;
	font-weight: 700;
	color: #fff;
	transition: 0.3s;
}
.logo__link:hover {
	text-decoration: none;
	color: #ffc107;
}
#menu-btn {
	width: 25px;
	cursor: pointer;
	overflow: hidden;
}
#menu-btn span {
	display: block;
	width: 100%;
	background-color: #fff;
	height: 2px;
	transition: 0.4s;
}
#menu-btn span ~ span {
	margin-top: 6px;
}
#menu-btn.open span:nth-child(2) {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
}
#menu-btn.open span:nth-child(1) {
	-webkit-transform: rotate(45deg) translate(6px, 5px);
	        transform: rotate(45deg) translate(6px, 5px);
}
#menu-btn.open span:nth-child(3) {
	-webkit-transform: rotate(-45deg) translate(7px, -5px);
	        transform: rotate(-45deg) translate(7px, -5px);
}
.menu {
	position: fixed;
	top: 100px;
	right: 0;
	width: 300px;
	overflow: auto;
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
	transition: 0.4s;
	padding: 20px 0 20px 20px;
	z-index: 2;
}
.menu.-active {
	-webkit-transform: translateX(0%);
	        transform: translateX(0%);
}
.menu ul {
	list-style: none;
	padding: 0;
	margin: 0;
}
.menu__item-link {
	margin-left: 10px;
	background-color: #495057;
	border-top-left-radius: 5px;
	border-bottom-left-radius: 5px;
	margin-bottom: 5px;
	transition: 0.5s;
	font-weight: 700;
	display: block;
	color: #fff;
	padding-left: 17px;
	padding-top: 10px;
	padding-bottom: 10px;
	box-shadow: 0px 4px 15px 5px rgba(0, 0, 0, 0.23);
}

.menu__item-link:hover {
	 margin-left: 0;
	 text-decoration: none;
	 background-color: #ffc107;
	 color: #000;
 }

.section-home__photo {
	width: 100%;
	position: relative;
	border-radius: 50%;
	box-shadow: 0px 11px 33px 5px rgba(0,0,0,0.23);
	background: #b9b9b9;
}
.section-home__photo:before {
	content: '';
	display: block;
	padding-bottom: 100%;
}
.section-home__photo:after {
	content: '';
	display: block;
	position: absolute;
	top: 0px;
	bottom: 0px;
	right: 0px;
	left: 0px;
	border-bottom: 5px solid #343a40;
	border-radius: 50%;
	-webkit-animation: spin 2s infinite linear;
	        animation: spin 2s infinite linear;
}
.section-home__photo span {
	position: absolute;
	top: 10px;
	left: 10px;
	right: 10px;
	bottom: 10px;
	background-image: url(/react-portfolio/static/media/my-photo.177c6033.jpg);
	background-size: cover;
	background-position: center;
	border-radius: 50%;
}
@media (max-width: 575px) {
	.section-home__photo {
		width: 70%;
		margin: 0 auto 30px;
	}
	.h1 {
		text-align: center;
	}
}
.section-home__content {
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
}
/*H1*/
.h1 {
	color: #343a40;
	font-weight: bold;
	font-size: 70px;
}
.h1 span {
	color: #ffc107;
}
.-mb-50 {
	margin-bottom: 50px;
}
/*H2*/
.-h2-gray {
	color: #a9a9a9;
}
/*BUTTON*/
.section-home__content-link {
	display: block;
	width: 100px;
	height: 100px;
	margin: 0 auto;
	position: relative;
}
.section-home__content-link:hover .section-home__content-linkname {
	-webkit-transform: scale(1.5);
	        transform: scale(1.5);
	background: rgba(255,193,7,1);
}
.section-home__content-spin {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	border-bottom-color: #ffc107;
	-webkit-animation: spin 2.5s infinite linear;
	        animation: spin 2.5s infinite linear;
}
.section-home__content-linkname {
	position: absolute;
	top: 21px;
	right: 21px;
	left: 21px;
	bottom: 21px;
	border-radius: 50%;
	background: rgba(255,193,7,0.9);
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	color: #ffffff;
	transition: 0.4s;
}
.section-home__content-spin:before {
	content: '';
	display: block;
	position: absolute;
	top: 5px;
	right: 5px;
	left: 5px;
	bottom: 5px;
	border-bottom-color: #ffe186;
	-webkit-animation: spin 2s infinite linear;
	        animation: spin 2s infinite linear;
}
.section-home__content-spin:after {
	content: '';
	display: block;
	position: absolute;
	top: 13px;
	right: 13px;
	left: 13px;
	bottom: 13px;
	border-bottom-color: #fde9ad;
	-webkit-animation: spin 1.5s infinite linear;
	        animation: spin 1.5s infinite linear;
}
.section-home__content-spin, .section-home__content-spin:before, .section-home__content-spin:after {
	border-radius: 50%;
	border-bottom-width: 3px;
	border-bottom-style: solid;
}
@-webkit-keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	0% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
@keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	0% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
@media (max-width: 1199px) {
	.h1 {
		font-size: 65px;
	}
}
@media (max-width: 991px) {
	.h1 {
		font-size: 42px;
	}
	.h2 {
		font-size: 26px;
	}
	.-mb-50 {
		margin-bottom: 30px;
	}
}
@media (max-width: 767px) {
	.section-home__content-link {
		display: none;
	}
}
@media (max-width: 575px) {
	.section-home__content-link {
		display: block;
	}
	.h2 {
		text-align: center;
	}
}

.section-home {
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}
@media (max-width: 575px) {
	.section-home {
		height: auto;
		padding: 60px 0 40px 0;
	}
}

.about_block__title, .about_block__description {
	text-align: right;
	display: block;
}
.about_block__title {
	text-transform: uppercase;
}
.about_block, .portfolio_block {
	position: relative;
	padding: 50px 0;
}
.block__background {
	position: absolute;
	top: 0;
	left: 0;
	width: 50%;
	height: 100%;
	background-color: #ced4da;
	z-index: -1;
	opacity: 0.8;
	border-top-right-radius: 15px;
	border-bottom-right-radius: 15px;
}
.-mb-15 {
	margin-bottom: 15px;
}
@media (max-width: 767px) {
	.block__background {
		display: none;
	}
	.about_block__title , .about_block__description {
		text-align: center;
	}
	.about_block__description {
		margin-bottom: 30px;
	}
	.about_block {
		padding: 20px 0;
	}
}
.skill__item-title {
	font-family: 'Orbitron', sans-serif;
}
.skill__item-title span {
	font-size: 22px;
	padding-left: 15px;
}


.footer {
	padding: 20px;
	background-color: #ced4da;
}
.footer span {
	float: right;
}
.section-about {
	padding-top: 100px;
}
.overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #000;
	opacity: 0;
	z-index: -1;
}
.overlay.-active {
	z-index: 1;
	opacity: 0.3;
}
.portfolio_block__photo {
	width: 100%;
	position: relative;
	display: block;
}
.portfolio_block__photo:hover > span:before {
	opacity: 0;
}
.portfolio_block__photo:hover > span {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}
.portfolio_block__photo:before {
	content: '';
	display: block;
	padding-bottom: 50%;
}
.portfolio_block__photo span {
	position: absolute;
	display: block;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transition: 0.3s;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
}
.portfolio_block__photo span:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #000;
	transition: 0.3s;
	opacity: 0.2;
}
.portfolio_block__content {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	height: 100%;
}
.content--link {
	color: #0056b3;
	position: relative;
	padding: 2px 5px;
	transition: 0.3s;
}
.content--link:before {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 0%;
	height: 100%;
	display: block;
	background-color: #ced4da;
	opacity: 0.8;
	z-index: -1;
	transition: 0.3s;
}
.content--link:hover {
	text-decoration: none;
	color: #000;
}
.content--link:hover:before {
	width: 100%;
}
@media (max-width: 767px) {
	.portfolio_block {
		padding: 20px 0;
	}
	.portfolio_block__photo {
		margin-bottom: 20px;
	}
	.portfolio_block__title, .link--position {
		text-align: center;
	}
}
.section-portfolio {
	padding-top: 100px;
}
